import { Canvas, useLoader, useFrame } from '@react-three/fiber'
import { OrbitControls } from '@react-three/drei'
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader'

import MetaSphere from './Bwob'
import { useRef } from 'react'


function CustomDeskScene(props) {
    const obj = useLoader(GLTFLoader, 'assets/desk_scene.glb')
    return <primitive {...props} object={obj.scene} scale={[1, 1, 1]} dispose={null} />
}


function SceneObjects() { 
    const ref = useRef();

    useFrame((state) => {
        const t = state.clock.getElapsedTime()
        // ref.current.rotation.x = Math.cos(t / 4) / 8
        ref.current.rotation.y = Math.sin(t / 3) / 8
        // ref.current.rotation.z = (1 + Math.sin(t / 1.5)) / 20
        // ref.current.position.y = (1 + Math.sin(t / 1.5)) / 10
    }); 

    return (<group ref={ref} dispose={null}>
        <MetaSphere position={[-1.5, 2.15, -1.5]} castShadow={true} />
        <CustomDeskScene position={[0, -1, -1]} />
    </group>);

}

function DeskScene() {
    return (
        <Canvas camera={{ fov: 60, near: 0.1, far: 1000, position: [-5, 1.7, -5] }}>
            <ambientLight intensity={1.0} />
            <pointLight position={[10, 10, -10]} />
            <SceneObjects />
            {/* <BakeTestScene /> */}
            {/* <Custom3DObject path="model_normalized.obj"/>  */}
            {/* <OrbitControls enableZoom={false} /> */}
        </Canvas> 
    )
}

export default DeskScene;