import logo from './logo.svg';
import DeskScene from './DeskScene';
import DeskSceneImage from './DeskSceneImage';
import ElevatorLevels from './ElevatorLevels';
import WorkBlock from './WorkBlock';
import IntroBlock from './IntroBlock'
import NavBar from './NavBar'
import { ChakraProvider } from '@chakra-ui/react'

import './App.css';
import { useEffect, useState, useRef } from 'react';
import { smootherstep } from 'three/src/math/MathUtils';

function App() {
  const d = new Date();
  const currentYear = d.getFullYear();
  const researchSection = useRef(null);
  const aboutSection = useRef(null);
  
  const [focus, set_focus] = useState(0);

  useEffect(() => { 
    window.addEventListener(
      'scroll',
      () => {
        if (window.scrollY >= researchSection.current.offsetTop) {
          set_focus(2);
        }
        else if (window.scrollY >= aboutSection.current.offsetTop) {
          set_focus(1);
        }
        else {
          set_focus(0);
        }
      }
    )
  })

  const scrollToResearch = () => {
    window.scrollTo({
      top: researchSection.current.offsetTop,
      behavior: "smooth"
    });
  }

  const scrollToAbout = () => {
    window.scrollTo({
      top: aboutSection.current.offsetTop, 
      behavior: "smooth"
    })
  }

  const scrollToTop = () => {
    window.scrollTo({
      top: 0,
      behavior: "smooth"
    });
  }

  return (
    <div className="full-screen" >
        <div className="elevator-levels">
          <ElevatorLevels focus_level={focus} button2semantics={[["top", scrollToTop], ["about", scrollToAbout], ["research", scrollToResearch]]} />
        </div>   
        <div className="header-chunk">
          <div className="center" > 
            <NavBar item2onClick={[["About", scrollToAbout], ["Research", scrollToResearch]]} />
          </div> 
          <div className="header">
            <h1 className="header-style main-header"> Hi, I'm Ian </h1>
            <h2 className="header-style sub-header"> I'm a Ph.D. student at Stanford working on 3D content generation.</h2>
          </div>
        </div>
        
        <div className="scene">
          <DeskScene />
        </div> 

        <div className="about-block" ref={aboutSection} >
          <h2 className="header-style sub-header-ff"> About </h2>
          <IntroBlock img_src="assets/profile_pic.png"
            description="I'm a Ph.D. student at Stanford doing research in AI
            and machine learning, advised by Leonidas Guibas, and a
            student researcher at Meta Reality Labs. I'm
            specifically interested in 3D content generation and
            technology that can advance the way we tell and experience
            stories. Previously, I studied applied math and CS at Columbia
            University, where I worked on projects around video understanding,
            robotics and neural network interpretability." />
        </div>

        <div className="research-block" ref={researchSection} >
          <h2 className="header-style sub-header-ff"> Research </h2>
          <WorkBlock img_src="assets/pictures/paper7.png" title="LADIS: Language Disentanglement for 3D Shape Editing" description="Natural language interaction is a promising direction for democratizing 3D shape design. However, existing methods for text-driven 3D shape editing face challenges in producing decoupled, local edits to 3D shapes. We address this problem by learning disentangled latent representations that ground language in 3D geometry. To this end, we propose a complementary tool set including a novel network architecture, a disentanglement loss, and a new editing procedure. Additionally, to measure edit locality, we define a new metric that we call part-wise edit precision. We show that our method outperforms existing SOTA methods by 20% in terms of edit locality, and up to 6.6% in terms of language reference resolution accuracy. Our work suggests that by solely disentangling language representations, downstream 3D shape editing can become more local to relevant parts, even if the model was never given explicit part-based supervision." />
          <WorkBlock img_src="assets/pictures/paper6.png" title="Language-Assisted 3D Shape Edits and Deformations" description="In this work, we address the task of Language-Assisted 3D Shape Edits and Deformations (which we name ChangeIt3D). Given a 3D representation of an object and free-form natural language describing desired changes or modifications to the shape of the object, the task is to transform the input object’s geometry in a manner that reflects the requested changes – for example, to modify a 3D chair model to make its legs thinner, or to open a hole in its back. To tackle this problem in a way that promotes open-ended language usage allowing fine-grained shape edits, we introduce the largest existing corpus of natural language describing shape differences, which we call ShapeTalk. This dataset contains over half a million discriminative utterances produced by contrasting the shapes of pairs of common 3D objects for a variety of object classes and degrees of similarity. We introduce metrics for the quantitative evaluation of language-assisted shape editing methods that reflect key desiderata within this editing setup. We also design an effective and modular framework for ChangeIt3D that can combine an arbitrary 3D generative model of shapes with our in-house, ShapeTalk-based, text-to-shape neural listener. Crucially, our modules are trained and deployed directly in a latent space of 3D shapes, bypassing the ambiguities of “lifting” 2D to 3D when using extant foundation models and thus opening a new avenue for 3D object-centric manipulation through language." />
          <WorkBlock img_src="assets/pictures/paper5.png" title="PartGlot: Learning Shape Part Segmentation from Language Reference Games" description=" How did we as humans come to understand and identify the parts of objects in the world around us when we have never been explicitly given thousands of groundtruth segmentations? One answer, perhaps, lies in the way we can use language to refer and differentiate between different objects. In this paper, we hope to imbue neural networks with a similar capability: namely, by training the model to correctly identify an object from a small group of objects satisfying a natural language reference, we show that surprisingly nice part segmentation masks can be extracted out of the model's attention over the spatial domain even when it was never given explicit part supervision." />
        </div>

        {/* <div className="full-app">
          <div className="bio center">
          <h2 className="infotext-dark"> I'm a Ph.D. student at Stanford doing research in AI
            and machine learning, advised by Leonidas Guibas. I'm specifically interested in 
            3D content generation and technology that can advance the way we tell and experience
            stories.</h2>
          </div> 
        </div> */}
      
        <footer> <small>&copy; Copyright {currentYear}, Ian Huang </small> </footer> 
      </div>
  );
}

export default App;
